<template>
  <breadcrumb-list :items="breadcrumbs">
    <router-link
      :to="{ name: 'hospital.create' }"
      class="font-medium text-white btn btn-sm bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
    >
      <i class="fa fa-plus fa-sm fa-fw"></i>
      {{ $t("labels.add") }}
    </router-link>
  </breadcrumb-list>

  <data-table :titles="titles" :repository="repository">
    <template #filters>
      <div class="py-3">
        <div class="grid grid-cols-1 gap-4 sm:grid-cols-3 sm:gap-5 lg:gap-6">
          <div class="block">
            <label for="textSearch">{{ $t("attributes.name") }}</label>
            <div class="w-full mt-1.5">
              <input
                v-model="repository.filters.search"
                id="textSearch"
                type="text"
                :placeholder="$t('placeholder.search')"
                class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              />
            </div>
          </div>

          <div class="block">
            <label for="selectCity">{{ $t("attributes.county") }}:</label>
            <div class="w-full mt-1.5">
              <vue-select
                :options="cities"
                label="name"
                :reduce="(city) => city.id"
                v-model="repository.filters.city_id"
                inputId="selectCity"
                :placeholder="$t('placeholder.select_city')"
              ></vue-select>
            </div>
          </div>

          <div class="block">
            <label for="selectCounty">{{ $t("attributes.county") }}:</label>
            <div class="w-full mt-1.5">
              <vue-select
                :options="counties"
                label="name"
                :reduce="(county) => county.id"
                v-model="repository.filters.county_id"
                inputId="selectCounty"
                :disabled="!(counties.length > 0)"
                :placeholder="
                  repository.filters.city_id
                    ? $t('placeholder.select_county')
                    : $t('placeholder.select_city')
                "
              ></vue-select>
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #body>
      <tr v-for="hospital in repository.getItems()" :key="hospital.id">
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ hospital.id }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ hospital.name }}
        </td>
        <td>{{ hospital.city.name }}</td>
        <td>{{ hospital.county.name }}</td>
        <td>
          <div class="flex space-x-2">
            <router-link
              :to="{ name: 'hospital.edit', params: { id: hospital.id } }"
              :title="$t('labels.edit')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
            >
              <v-icon name="pencil-square" size="5"></v-icon>
            </router-link>

            <v-button
              color="error"
              icon
              :size="10"
              :title="$t('labels.delete')"
              @click="deleteModal(hospital.id)"
            >
              <v-icon name="archive-delete" size="5"></v-icon>
            </v-button>
          </div>
        </td>
      </tr>
    </template>
  </data-table>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="error" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import FilterRepository from "@/core/FilterRepository";
import api from "@/core/Api";
import Toast from "@/core/Toast";

export default {
  name: "HospitalList",
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.hospitals"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        {
          column: "name",
          title: this.$t("attributes.hospital"),
          sortable: true,
        },
        { title: this.$t("attributes.city") },
        { title: this.$t("attributes.county") },
        { title: this.$t("labels.actions") },
      ],
      repository: new FilterRepository("/hospitals", {
        search: "",
        city_id: null,
        county_id: null,
      }),
      cities: null,
      delete: null,
    };
  },
  methods: {
    deleteModal(id) {
      this.delete = id;
      this.$refs.modal.toggle();
    },
    deleteData() {
      api
        .post(`/hospital/delete/${this.delete}`)
        .then(() => {
          Toast.fire(this.$t("response.success.delete"), "success");
          this.$refs.modal.toggle();
          this.repository.fetch();
        })
        .catch((error) => {
          Toast.fire(
            this.$t("response.errors." + error.response.status),
            "error"
          );
        });
    },
  },
  created() {
    this.repository.fetch();
    api.get("/cities").then(({ data }) => {
      this.cities = data;
    });
  },
  computed: {
    counties() {
      if (this.repository.filters.city_id) {
        const city = this.cities.find(({ id }) => {
          return this.repository.filters.city_id === id;
        });

        return city.counties;
      }

      return [];
    },
  },
};
</script>
